.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal__content {
  padding: 25px;
  background-color: #fff;
  border-radius: 16px;
  min-width: 250px;
}

.active {
  display: flex;
  justify-content: center;
  align-items: center;
}